import React, { Component } from 'react';
import styled from "styled-components";
import AboutMeTitle from './AboutMeTitle.js';
import ProfileBox from './ProfileBox.js';
import AssistBox from './AssistBox.js';

const AboutMeStyle = styled.div`
    width: 100%;
    height: 100%;
    margin-top: 30px;
    overflow: hidden;

`;

const AboutmeProfileLocation = styled.div `
    width: 100%;
    height: 100%;

    @media (min-width:0px) and (max-width: 680.9px) {
        position: absolute;
        width: 320px;
        height: 100%;
        
        left: 50%;
        margin-left: -150px;
    }

    @media (min-width:681px) and (max-width:767.9px) {
        margin-left: 100px;
    }
    
    @media (min-width:768px) and (max-width:1023.9px) {
        margin-left: 100px;
    }

    @media (min-width: 1024px) and (max-width: 1599.9px) {
        margin-left: 307px;
    }
    
    @media (min-width: 1600px) {
        margin-left: 20.3%;
    }
`;

const AssistMent = styled.div `
    width: 100%;
    height: 100%;

    @media (min-width:0px) and (max-width:680.9px) {
        font-size: 15.5px;
        margin-top: 330px;
    }
`;

class AboutMeBox extends Component {
    render() {
        return (
        <div>
            <AboutMeStyle>
                <AboutmeProfileLocation>
                    <div>
                        <AboutMeTitle></AboutMeTitle>
                        <AssistBox></AssistBox>
                        <br></br>
                        <ProfileBox></ProfileBox>
                    </div>
                </AboutmeProfileLocation>
                <AssistMent>
                </AssistMent>
            </AboutMeStyle>
        </div>
        )
    }
}

export default AboutMeBox;