import React, { Component } from 'react';
import styled from "styled-components";

const AssistTitleStyle = styled.div `
    width: 100%;
    height: 100%;
    color: #black;
    font-size: 25px;
    text-decoration: none;
    transition: 1s;
    font-family: 'Do Hyeon', sans-serif;
`;

const AssistMentStyle = styled.div`
    font-size: 18px;
    font-family: 'Noto Sans KR', sans-serif;
    color: #747474;
    line-height: 30px;

    @media (min-width:0px) and (max-width: 1023.9px) {
        font-size: 12.5px;
    }
`;

class AssistBox extends Component {  
    render() {
        return (
        <div>
            <AssistTitleStyle>
                <p>"같이 일하고 싶은 개발자"</p>
            </AssistTitleStyle>
            <AssistMentStyle>
                <p>
                    여러 고개를 넘어 드디어 정상에 도착하였습니다.<br />
                    정상에 도착하니 보이지 않았던 큰 산이 보이기 시작하였고,<br />
                    새로운 정상을 향해 또다시 걷기 시작하였습니다.<br />  
                    그 산의 이름은 <b>"같이 일하고 싶은 개발자"</b> 입니다.
                </p>
            </AssistMentStyle>
        </div>
        )
    }
}

export default AssistBox;